<template>
  <div>
    <el-card>
      <!-- 搜索框（学科名称） -->
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="新增学科">
          <el-input
            v-model="form.name"
            style="width: 20%; margin-right: 10px"
          ></el-input>
          <el-button @click="onSubmit">清除</el-button>
          <el-button type="primary">搜索</el-button>
          <el-button
            class="xuekean"
            type="success"
            icon="el-icon-edit"
            @click="addlist"
            >新增学科</el-button
          >
        </el-form-item>
      </el-form>
      <!-- 消息提示文案 -->
      <el-alert
        style="margin-bottom: 15px;"
        :title="`数据一共${total}条`"
        type="info"
        show-icon
      ></el-alert>
      <!-- 表格 -->
      <el-table :data="tableData" style="width: 100%">
        <el-table-column type="index" label="序号"> </el-table-column>
        <el-table-column prop="subjectName" label="学科名称"></el-table-column>
        <el-table-column prop="username" label="创建者"> </el-table-column>
        <el-table-column label="创建日期" width="200">
          <template slot-scope="scope">
            <div>
              {{
                scope.row.addDate | parseTimeByString('{y}-{m}-{d} {h}:{i}:{s}')
              }}
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="isFrontDisplay" label="前台是否显示">
          <template slot-scope="scope">
            <span v-if="scope.row.isFrontDisplay === 0">是</span>
            <span v-else>否</span>
          </template>
        </el-table-column>
        <el-table-column prop="twoLevelDirectory" label="二级目录">
        </el-table-column>
        <el-table-column prop="tags" label="标签"> </el-table-column>
        <el-table-column prop="totals" label="题目数量"> </el-table-column>
        <el-table-column label="操作" width="240">
          <template slot-scope="scope">
            <span
              class="cz"
              @click="
                $router.push({
                  path: 'directorys',
                  query: { id: scope.row.id, name: scope.row.subjectName }
                })
              "
              >学科分类</span
            >
            <span
              class="cz"
              @click="
                $router.push({
                  path: 'tags',
                  query: { id: scope.row.id, name: scope.row.subjectName }
                })
              "
              >学科标签</span
            >
            <span class="cz" @click="amendBtn(scope.row)">修改</span>
            <span class="cz" @click="deleteBtn(scope.row.id)">删除</span>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
        class="pages"
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="100"
        layout="  prev, pager, next,sizes, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!-- 点击新增弹出的对话框 -->
    <!-- <el-dialog
      title="修改学科"
      :visible.sync="dialogVisible"
      width="30%"
      @close="onClose"
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="学科名称" prop="subjectName">
          <el-input v-model="ruleForm.subjectName"></el-input>
        </el-form-item>
        <el-form-item label="是否显示">
          <el-switch v-model="val" @change="changeBtn"></el-switch>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog> -->
    <!-- 点击修改弹出的对话框 -->
    <el-dialog
      title="修改学科"
      :visible.sync="dialogVisible"
      width="30%"
      @close="onClose"
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="学科名称" prop="subjectName">
          <el-input v-model="ruleForm.subjectName"></el-input>
        </el-form-item>
        <el-form-item label="是否显示">
          <el-switch
            active-color="#13ce66"
            inactive-color="#ff4949"
            v-model="val"
            @change="changeBtn"
          ></el-switch>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
    <!-- 点击添加弹出的对话框 -->
    <el-dialog
      title="新增学科"
      :visible.sync="dialogVisibles"
      width="30%"
      @close="onClose"
    >
      <el-form
        :model="ruleForms"
        :rules="rules2"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="学科名称" prop="subjectName">
          <el-input v-model="ruleForms.subjectName"></el-input>
        </el-form-item>
        <el-form-item label="是否显示">
          <el-switch
            active-color="#13ce66"
            inactive-color="#ff4949"
            v-model="val"
            @change="changeBtn"
          ></el-switch>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisibles = false">取 消</el-button>
        <el-button type="primary" @click="submitForms('ruleForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
// 引入Api
import { list, remove, update, add } from '@/api/hmmm/subjects'
export default {
  data () {
    return {
      // 搜索的表单值
      form: {
        name: ''
      },
      // 表格数据
      tableData: [],
      // 对话框数据
      dialogVisible: false,
      dialogVisibles: false,
      // 弹出框
      rules: {
        subjectName: [
          { required: true, message: '请输入修改内容', trigger: 'blur' }
        ]
      },
      rules2: {
        subjectName: [
          { required: true, message: '请输入修改内容', trigger: 'blur' }
        ]
      },
      ruleForms: {
        // 弹出框表单值
        subjectName: '',
        // 弹出框是否显示
        isFrontDisplay: null
      },
      ruleForm: {
        // 弹出框表单值
        subjectName: '',
        // 弹出框是否显示
        isFrontDisplay: null,
        // 点击修改的id
        id: null
      },
      // 分页总条数
      total: 0,
      val: false,
      // 修改弹框
      dialogVisible: false
    }
  },
  created () {
    this.courseDetailsApi()
  },
  methods: {
    // 点击清除触发的事件
    onSubmit () {
      console.log('submit!')
    },
    // 获取数据的函数
    async courseDetailsApi (obj) {
      const { data } = await list(obj)
      // console.log(data)
      this.tableData = data.items
      this.total = data.counts
      console.log(data, 11111)
    },
    async submitForms () {
      const { data } = await add(this.ruleForms)
      console.log(data)
      this.dialogVisibles = false
      this.courseDetailsApi()
    },
    // 点击修改的按钮
    amendBtn (scope) {
      this.dialogVisible = true
      console.log(scope.isFrontDisplay)
      if (scope.isFrontDisplay === 0) {
        this.val = true
      } else {
        this.val = false
      }
      console.log(scope)
      this.ruleForm = scope
      console.log(this.ruleForm, 777)
    },

    // 点击添加按钮
    addlist (scope) {
      this.dialogVisibles = true
      if (scope.isFrontDisplay === 0) {
        this.val = true
      } else {
        this.val = false
      }
    },

    // 点击删除按钮
    deleteBtn (id) {
      // console.log(id)
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          // 点击删除对应的内容
          remove({ id }),
          // 刷新页面
          this.courseDetailsApi()
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    // 修改弹出框的提交表单事件(确定按钮)
    // submitForm(formName) {
    //   this.dialogVisible = false
    //   this.$refs[formName].validate((valid) => {
    //     if (valid) {
    //       // 判断id值是否为空
    //       if (this.ruleForm.id == null) {
    //         // 为空发送增加的请求
    //         add({
    //           id: null,
    //           subjectName: this.ruleForm.name,
    //           isFrontDisplay: this.ruleForm.delivery,
    //         })
    //         // 清空表单的值
    //         this.ruleForm.name = ''
    //       } else {
    //         // 不为空发送修改的请求
    //         update(this.ruleForm)
    //       }
    //       // 刷新页面
    //       this.courseDetailsApi()
    //     } else {
    //       this.$message({ message: '操作失败' })
    //       return false
    //     }
    //   })
    // },

    // 分页
    handleSizeChange (val) {
      // console.log(`每页 ${val} 条`)
      this.courseDetailsApi({ pagesize: val })
    },
    handleCurrentChange (val) {
      // console.log(`当前页: ${val}`)
      this.courseDetailsApi({ page: val })
    },
    changeBtn (value) {
      if (value) {
        this.ruleForms.isFrontDisplay = 1
      } else {
        this.ruleForms.isFrontDisplay = 0
      }
    },
    // 清空表单
    onClose () {
      this.$refs.ruleForm.resetFields()
    },
    // 点击确认修改按钮
    async submitForm () {
      const res = await update(this.ruleForm)
      this.dialogVisible = false
      this.courseDetailsApi()
    },

    // 点击确认添加按钮
    async submitForms () {
      const res = await add({
        subjectName: this.ruleForms.subjectName,
        isFrontDisplay: this.val == true ? 1 : 0
      })
      this.dialogVisibles = false
      this.courseDetailsApi()
      this.onClose()
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.xuekean {
  position: absolute;
  top: 0;
  right: 0;
}
.cz {
  margin: 0 3px;
  color: #409eff;
  cursor: pointer;
}
.pages {
  float: right;
  margin: 10px 0;
}
</style>
